import React from "react";
import { Button } from "react-bootstrap";
import "./DownloadCV.css";

const DownloadCV = () => {
  return (
    <div className="d-flex" style={{ marginLeft: "-110px" }}>
      <Button
        variant="primary"
        size="lg"
        active
        onClick={() =>
          window.open(
            "https://drive.google.com/file/d/1eywvbirZ7kLrQ_QvQ8ZGDvFFUYs22-x7/view?usp=sharing",
            "_blank"
          )
        }
        className="downloadCV__button"
      >
        Download CV
      </Button>
    </div>
  );
};

export default DownloadCV;
