import React from "react";
import linkedinLogo from "../images/linkedinLogo.png";
import githubLogo from "../images/githubLogo.png";
import "./Socials.css";

function Socials() {
  return (
    <div className="Socials">
      <div className="Socials__linkedin">
        <img
          src={linkedinLogo}
          onClick={() =>
            window.open(
              "https://www.linkedin.com/in/nischal-tripathi-188a92a4/",
              "_blank"
            )
          }
          className="Socials__linkedinlogo"
          alt=""
        />
        <img
          className="Socials__githublogo"
          onClick={() =>
            window.open("https://github.com/nischaltripathi", "_blank")
          }
          src={githubLogo}
          alt=""
        />
      </div>
    </div>
  );
}

export default Socials;
