import React, { useEffect, useState } from "react";
import "./Projects.css";
import ProjectCard from "./ProjectCard";
import ReactCardCarousel from "react-card-carousel";
import amazonImg from "../images/amazon-clone.jpg";
import rosterImg from "../images/RosterWebsite.jpg";
import whatsappImg from "../images/whatsappClone.jpg";
import netflixImg from "../images/netflix-project.jpg";
function Projects() {
  const [offsetY, setOffsetY] = useState(0);
  const handleScroll = () => setOffsetY(window.pageYOffset);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="projects">
      <ReactCardCarousel disable_box_shadow="false">
        <ProjectCard
          image={amazonImg}
          weblink="https://clone-app-31d82.firebaseapp.com/"
          gitlink="https://github.com/nischaltripathi/amazonClone"
          title="AMAZON CLONE"
          description="It is a dummy website, where you signup/login, can add items to the your cart,
             go to the checkout page and confirm your order by entering
            test card details provided by stripe. You can check the source code and deployed website by clicking on the icons given below"
        />
        <ProjectCard
          image={rosterImg}
          weblink="https://roster-system.herokuapp.com/"
          gitlink=""
          title="Roster Website"
          description="Managers can login to make roster for employees and employees can login to get their roster for the week, get notification, and mark task complete."
        />
        <ProjectCard
          image={whatsappImg}
          weblink=""
          gitlink="https://github.com/nischaltripathi/whatsapp-clone"
          title="WhatsApp Web CLone"
          description="You can login using your google account, and make groups and chat on it. I've added time stamp feature as well so that poeple can see at what time message was sent"
        />
        <ProjectCard
          image={netflixImg}
          weblink="https://netflix-clone-4e9ae.firebaseapp.com/"
          gitlink=""
          title="Netflix clone"
          description="This is dummy website which uses TMBD API for fetching movie's data and display like netflix does."
        />
      </ReactCardCarousel>
    </div>
  );
}

export default Projects;
