import React from "react";
import { Card } from "react-bootstrap";
import { AiFillGithub } from "react-icons/ai";
import { CgWebsite } from "react-icons/cg";
import ReactTooltip from "react-tooltip";
import "./ProjectCard.css";

function ProjectCard({ image, title, description, gitlink, weblink }) {
  return (
    <div
      style={{ height: "80%", width: "80%", marginTop: "35%" }}
      className="p-5 container justify-content-center d-flex "
    >
      <Card
        className="ProjectCard"
        style={{
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          backgroundSize: "cover",
        }}
      >
        <Card.Img src={`${image}`} className="ProjectCard__image" />

        <Card.ImgOverlay className="ProjectCard__overlay">
          <Card.Title style={{ marginTop: "2rem" }} className="text-center">
            {title}
          </Card.Title>
          <Card.Text
            style={{ marginTop: "2rem" }}
            className="p-2 d-flex flex-column align-middle justify-content-center"
          >
            {description}
            <br />
            <div className="text-center">
              <AiFillGithub
                data-tip
                data-for="github"
                className="ProjectCard__gitlogo text-center"
                onClick={() => window.open(gitlink, "_blank")}
                size="40%"
              />
              <ReactTooltip id="github" type="error">
                <span>GitHub</span>
              </ReactTooltip>
              <CgWebsite
                data-tip
                data-for="livewebsite"
                class="ProjectCard__webIcon"
                onClick={() => window.open(weblink, "_blank")}
              />
              <ReactTooltip id="livewebsite" type="error">
                <span>Live Website</span>
              </ReactTooltip>
            </div>
          </Card.Text>
        </Card.ImgOverlay>
      </Card>
    </div>
  );
}

export default ProjectCard;
